.singlePost {
  flex: 9;
}
.singlePostWrapper {
  padding: 20px;
}

.singlePostImg {
  width: 100%;
  height: 400px;
  border-radius: 5px;
  object-fit: cover;
  object-position: 50% 25%;
}

.singlePostTitle {
  text-align: center;
  margin: 15px;
  font-family: "Lora", serif;
  font-size: 32px;
  font-weight: 500;
}

.singlePostEdit {
  float: right;
  font-size: 20px;
}

.singlePostIcon {
  cursor: pointer;
  transition: all 0.5s ease;
}

.singlePostIcon:first-child {
  margin-right: 15px;
}

.singlePostIcon:first-child:hover {
  color: #70b20d;
}

.singlePostIcon:last-child:hover {
  color: #d63820;
}

.singlePostInfo {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  color: #c9ab7b;
  font-family: "Varela", sans-serif;
}

.singlePostAuthor {
  margin-left: 5px;
}

.singlePostDesc > p {
  color: #666;
  font-size: 18px;
  line-height: 25px;
  text-indent: 35px;
  margin-top: 5px;
}

.singlePostDesc::first-letter {
  font-size: 30px;
  font-weight: 600;
}
